*{
	padding: 0%;
	margin: 0;
	border: 0;

}
*, *:before, *:after{
	-moz-box-sizing: border-box;
	-webkit-box-sizing: border-box;
	box-sizing: border-box;
}
:focus, :active{outline: none;}
a:focus, a:active{outline: none;}
nav,footer,header,aside{display: block;}
html,body{
	font-size: 100%;
	line-height: 1;
	font-size: 14px;
	-ms-text-size-adjust: 100%;
	-moz-text-size-adjust: 100%;
	-webkit-text-size-adjust: 100%;

}
input,button,textarea{
	font-family: inherit;
}
input::-ms-clear{display: none;}
button{cursor: pointer;}
button::-moz-focus-inner {padding: 0; border: 0;}
a, a:visited{text-decoration: none;}
ul li{list-style: none;}
img{vertical-align: top;}

h1,h2,h3,h4,h5,h6{font-size: inherit; font-weight: 400;}

a{color: white;}
@font-face {
  font-family: 'AmazonR';
  src: url('./font/AmazonEmber_Rg.ttf') format('truetype');
}

@font-face {
  font-family: 'AmazonB';
  src: url('./font/AmazonEmber_Bd.ttf') format('truetype');
}

body {
  margin: 0;
  font-family: 'AmazonR'; /* Убедитесь, что имя шрифта заключено в кавычки */
  font-size: 16px;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

h2{
  font-family: 'AmazonB';
  font-weight: 700;
}

.main__container{
  max-width: 1000px;
  padding: 15px;
  margin: 0 auto;
}
.main-block__title{
  font-family: "AmazonB";
  font-size: 24px;
  line-height: 32px;
  margin-bottom: 10px;
}
.block-info__info{
  display: flex;
  width: 100%;
  justify-content: space-between;
  align-items: center;
  gap: 15px;
  padding-bottom: 15px;
}
.block-info__block{
  display: flex;
  gap: 15px;
}
.block-info__block div:nth-child(1){
  position: relative;
  padding-right: 20px;
}
.block-info__block div:nth-child(1)::after{
  content: '';
  height: 17px;
  border-radius: 50%;
  background-color: gray;
  opacity: 0.6;
  width: 2px;
  position: absolute;
  top: 0;
  right: 0;
}
.block-info__sublock-btn{
  background: none;
  padding: 7px;
  border: 1px solid rgb(78, 76, 76,0.7);
  border-radius: 10px;
  font-family: "AmazonB";
}

.main-block__block{
  width: 100%;
  border: 1px solid rgb(128, 128, 128,0.6);
  border-radius: 10px;
  padding: 15px;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  overflow: hidden;
  margin-bottom: 20px;
}
.main-block__block-section{
  display: flex;
  flex-direction: column;
  gap: 10px;
}
.main-block__block-title{

}
.main-block__block-text-card{
  display: flex;
  gap: 10px;
  align-items: center;
}
.main-block__block-card{
  border: 1px solid red;
  padding: 2px;
}
.main-block__block-list{
  display: flex;
  flex-direction: column;
  gap: 7px;
}
.main-block__block-li{
  display: flex;
  justify-content: space-between;
}
.main-block__block-li-black{
  font-family: "AmazonB";
  margin-top: 3px;
}
.main-block__block-li-green{
  font-family: "AmazonB";
  color: green;
}
.main-block__block-section-transition{
  margin-top: 20px;
  position: relative;
  grid-column: 1/4;
}
.main-block__block-section-transition::after{
  content: '';
  position: absolute;
  top: 0;
  left: -100px;
  width: 1000%;
  height: 1px;
  background-color:rgb(128, 128, 128,0.6);
}
.main-block__block-section-transition-block{
  padding: 10px;
  align-self: center;
  padding-top: 25px;
}
.main-block__block-section-transition-btn{
  position: relative;
  margin-left: 20px;
  background: none;
  color:#08a19c;
  font-family: "AmazonB";
}
.main-block__block-section-transition-block-open{
  margin-top: 10px;
  padding-left: 7px;
  display: flex;
  flex-direction: column;
  gap: 5px;
}
.main-block__block-section-transition-btn.active::after,
.main-block__block-section-transition-btn.active::before{
  transition: 0.3s;  
}
.main-block__block-section-transition-block-refund{
  display: flex;
  gap: 5px;
}
.main-block__block-section-transition-block-shipped{
  display: flex;
  gap: 5px;
}
.main-block__block-section-transition-block-shipped p:nth-child(1){
  color: rgb(128, 128, 128,0.8);
}
.main-block__block-section-transition-block-shipped p:nth-child(3){
 margin-left: 15px;
}
.main-block__block-section-transition-block-total{
  margin-top: 10px;
  display: flex;
  gap: 5px;
}
.main-block__block-section-transition-btn::after{
  content: '';
  position: absolute;
  transition: 0.3s;
  top: 3px;
  left: -16.3px;
  width: 2px;
  height: 10px;
  background-color: black;
  transform: rotate(-45deg);
  border-radius: 20px 20px 0  0 ;
}
.main-block__block-section-transition-btn::before{
  content: '';
  position: absolute;
  top: 3px;
  transition: 0.3s;
  left: -10px;
  width: 2px;
  height: 10px;
  background-color: black;
  transform: rotate(45deg);
  border-radius: 20px 20px 0  0 ;
}
.main-block__block-section-transition-btn.active::after{
  content: '';
  position: absolute;
  top: 3px;
  left: -16.3px;
  width: 2px;
  height: 10px;
  background-color: black;
  transform: rotate(45deg);
  border-radius: 20px 20px 0  0 ;
}
.main-block__block-section-transition-btn.active::before{
  content: '';
  position: absolute;
  top: 3px;
  left: -10px;
  width: 2px;
  height: 10px;
  background-color: black;
  transform: rotate(-45deg);
  border-radius: 20px 20px 0  0 ;
}
.main-block__subblock{
  display: flex;
  justify-content: space-between;
  gap: 30px;
}
.main-block__subblock-item{
  display: flex;
  gap: 10px;
}
.main-block__subblock-title{
    font-size: 22px;
    
    margin-bottom: 30px;
}
.main-block__subblock-item-title{
  color: #08a19c;
}
.main-block__subblock-item-block{
  display: flex;
  flex-direction: column;
  gap: 7px;
}
.main-block__subblock-item-seller{
  color: rgb(128, 128, 128,0.8);
  display: flex;
  gap: 5px;
}
.main-block__subblock-item-cost{
  color: rgb(198, 1, 1);
}
.main-block__subblock-item-codit{
  display: flex;
  gap: 5px;
  color: rgb(101, 100, 100);
}
.main-block__subblock-item-condit-bfw{
  font-family: "AmazonB";
}
.main-block__subblock-item-btn button{
  background-color: gold;
  padding: 15px 10px;
  border-radius: 10px;
  position: relative;
  padding-left: 50px;
}
.main-block__subblock-item-btn button::after{
  content: '';
  position: absolute;
  z-index: 22;
  top: 7px;
  left: 7px;
  width: 35px;
  height: 35px;
  background-image: url('../src/free-icon-refund-3745556.png');
  background-size: contain;
  background-repeat: no-repeat;
}
.main-block__subblock-section-btns{
  display: flex;
  flex-direction: column;
  gap: 7px;
  padding-right: 10px;
}
.main-block__subblock-buttons:nth-child(1){
  margin-bottom: 10px;
}
.main-block__subblock-buttons{
  padding: 7px 30px;
  border-radius: 10px;
  background: none;
  border: 1px solid rgb(128, 128, 128,0.6);
}
.main-block__subblock-buttons-ylw{
  background-color: gold;
}